<template>
  <div id="app">
    <Header :input-change="search" :market-info="marketInfo" />
    <alert
      v-if="error"
      title="Oooops!!"
      :error="error"
      :dismiss="dismiss"
      type="danger">
    </alert>
    <div id="page">
      <transition name="slide-fade">
        <router-view />
      </transition>
    </div>
    <Loader :is-loading="isLoading"/>
  </div>
</template>

<script>
import Header from '@/components/common/header/Header';
import Loader from '@/components/common/loader/Loader';
import Alert from '@/components/common/alert/Alert';

import { mapGetters } from 'vuex';

export default {
  name: 'app',
  computed: {
    ...mapGetters({
      isLoading: 'isLoading',
      error: 'getError',
      marketInfo: 'getMarketInfo'
    })
  },
  created: function() {
    this.$store.dispatch('getGlobalMarketInfo');
  },
  methods: {
    search: function(searchText) {
      this.$store.dispatch('searchTickers', searchText);
    },
    dismiss: function() {
      this.$store.dispatch('setError', null);
    }
  },
  components: { Header, Loader, Alert }
};
</script>

<style>
body {
  background-color: #343a40 !important; /* Avoid reboot.css overriding background color */
  overflow-x: hidden;
}
</style>

<style lang="scss">
@import '../../node_modules/bootstrap/scss/bootstrap.scss';

.slide-fade-enter-active {
  transition: all 0.4s ease;
}

.slide-fade-enter {
  transform: translateX(20px);
  opacity: 0;
}

body {
  padding-top: 3.5rem;
}

.pointer {
  cursor: pointer;
}
</style>



